*,
*::after,
*::before {
  box-sizing: border-box;
}

:root {
  font-size: 16px;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
}

.img-container {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
}

canvas {
  display: block;
}

body {
  --color-text: #fff;
  --color-bg: #0e0e0f;
  --color-link: #826437;
  --color-link-hover: #fff;
  color: var(--color-text);
  background-color: var(--color-bg);
  font-family: moderno-fb, "Big Caslon", "Book Antiqua", "Palatino Linotype",
    Georgia, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
}

a {
  text-decoration: none;
  color: var(--color-link);
  outline: none;
}

a:hover,
a:focus {
  color: var(--color-link-hover);
  outline: none;
}

.container {
  width: 50%;
}

.frame {
  height: 100%;
  padding: 0px;
  text-align: center;
  position: relative;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 53em) {
  .frame {
    height: 100%;
    width: 100%;
    position: fixed;
    text-align: left;
    z-index: 10000;
    top: 0;
    left: 0;
    display: grid;
    align-content: space-between;
    max-width: none;
    padding: 2rem 3rem;
    pointer-events: none;
    grid-template-columns: 75% 25%;
    grid-template-rows: auto auto auto;
    grid-template-areas:
      "title links"
      "content content"
      "github demos";
  }
}
